@import url(https://fonts.googleapis.com/css2?family=Syne+Tactile&display=swap);
*{
  margin: 0;
  padding: 0;
}
html{
  box-sizing: border-box;
}
img .img-fluid{
 position: absolute;
}
.domainName{
    position: absolute;
    margin-top: 32px;
    margin-left: 10px;
    font-size: 20px;
}
body {
  background:  #5D3F6A;
  color: #fff;
}
.carousel-item {
  height: 450px;
 }

.card-image{
  background-image: url(/static/media/home-bg.2f1febe1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
}
.card{
  background-color: #6C7A89;
}
.card-body{
  color: white;
}
.btn-home{
  overflow: hidden;
}
.btn-home a{
  color: #fff !important;
  font-weight: bold;
}
.card{
  background: rgb(93, 63, 106, 0.9);
}
.btn-link{
  text-decoration: none;
}
.explorer-section{
  background-color: rgb(93, 63, 106, 0.6);
  -webkit-clip-path: polygon(0 100%, 50% 0, 100% 100%, 0 100%);
          clip-path: polygon(0 100%, 50% 0, 100% 100%, 0 100%);
}

/* COUNTER SECTION */
.explorer-section a h2:hover{
  color: #fff !important;
  transition: all ease .6s;
  padding-top: 10px;
}
.counter-section i{
  background-color: #5D3F6A;
  padding: 15px;
  border-radius: 50%;
  margin-top: 15px;
  font-size: 20px;
}
.counter-section i:hover{
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  background-color: #6C7A89;
  color: #fff;
  /* Safari */
  -webkit-transform: rotate(-360deg);
  transition: all ease .6s;
  /* Firefox */
  -moz-transform: rotate(-360deg);

  /* IE */
  -ms-transform: rotate(-360deg);

  /* Opera */
  -o-transform: rotate(-360deg);
}
.h3-tag h3{
  color: #5B3256;
}
/* END OF COUNTER SECTION */
/* CONTACT SECTION */
.contact{
  background-image: url(/static/media/contact-two.fbedc7e2.jpg);
  background-attachment: fixed;
  background-position: center center;
  background-repeat: no-repeat;
  color: #fff;
  width: 100%;
}
.contact-section{
  margin-top: 20px !important;
}
/* CONTACT SECTION */
/* NAVBAR SECTION */
.bg-dark{
  opacity: 0.8;
  background: #000 !important;
  color: #fff;
}
.navbar{
  padding-bottom: 0.7rem !important;
}
.navbar-nav{
  position: relative;
  padding-top: 20px;
}
.navbar a{
  color: rgb(172, 175, 177) !important;
}
.nav-item{
  padding-top: 20px;
}
.nav-item a{
  color: aliceblue !important;
}
.justify-content-end{
  position: absolute;
  margin-top: -40px;
  right: 0;
}
.nav-item a:hover{
  font-weight: bold;
  border-bottom: 1px solid #565387;
  background-color: rgb(93, 63, 106, 0.8);
  border-radius: 0 20px 0 20px;
}
 .social-list .nav-link{
  padding: 5px !important;
  border: 1px solid #5B3256;
  margin-left: 5px;
}
.social-list .nav-link i:hover{
  background-color: #5B3256;
  padding: 7px;
  border-radius: 0 25px 0 25px;
  transition: all ease .6s;
}

/* END OF COUNTER SECTION */
/* END OF COUNTER SECTION */

/* app Slide */
.appSlide{
  margin-top: 0px !important;
  padding-top: 20px !important;
}
#footer-section{
  background-image: url(/static/media/footer.c423ab9f.jpg);
  height: 55vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.footer a{
  border: 3px solid #fff;
  margin: 5px;
  border-radius: 10px 0 10px 0;
}
.footer a:hover{
  transform:rotate(360deg);
  -webkit-transform:rotate(360deg);
  transform: translateY(10px);
  background-color: #5B3256;
  transition: all ease .6s;
}
.address{
  position: relative;
  margin-top: -95px;
  color: #fff;
}
.address p{
  margin: 0;
  padding: 0;
 font-weight:bold;
}
.menu_active {
  font-weight: bold;
  border-bottom: 3px solid #565387;
  background-color: rgb(93, 63, 106, 0.8);
  border-radius: 0 20px 0 20px;
}
.con-info{
  font-size: 14px;
}
.modal-content{
  background-color: rgb(93, 63, 106, 0.8);
}
