    }
    .navbar-nav .social-list{
      -webkit-justify-content: flex-start !important;
              justify-content: flex-start !important;
      margin-left: 80px;
      margin-top: -75px;
      z-index: -555;
    }
  .social-list p{
      padding: 0 !important;
    } 
   
  }
  @media only screen and (max-width: 767px){

    .navbar-nav .first-item{
      margin-top: -42px;
    }
    .footer-body .nav-link{
      margin: 0 10px;
      padding: 0;
    }
    .home-button{
      text-align: center;
    }
  }
  @media only screen and (max-width: 640px){
    #footer-section{
      height: 72vh !important;
    }
  }
  @media only screen and (max-width: 575px){
    .navbar-nav{
      position: relative;
    }
    .justify-content-end{
      position: absolute;
      top: -15px;
      right: 35%;
    }
    .header .head-line h3{
      position: absolute;
      font-size: 20px;
      margin-top: -34px;
    }
    .header .head-line h1{
      position: absolute;
      margin: 0;
      font-size: 16px;
    }
    .explorer-section h2{
      margin-top: 10px;
      font-size: 20px;
    }
    #footer-section{
      height: 70vh !important;
    }
    .navbar-nav .first-item{
      margin-top: 0px;
    }
    .home-button .btn-two{
      text-align: center;
      width: 100%;
    }
    .header{
      text-align: center;
    }
    .header .head-line h3{
      padding-top: 25px;
    }
    .header .head-line h1{
      padding-top: 35px;
    }
    .header .head-line h2{
      margin-top: 45px;
      margin-bottom: -25px;
      padding-top: 70px;
    }
  }
  @media only screen and (max-width: 350px){
    .explorer-section h2{
      margin-top: 10px;
      font-size: 16px;
    }
    .navbar-nav{
      position: relative;
      top: -15%;
    }
    .navbar {
      padding-bottom: 1.5rem !important;
    }
    .justify-content-end{
      position: absolute;
      top: 108%;
      right: 35%;
    }
    .social-list .nav-link{
      border: 1px solid #5B3256;
    }
    .social-list .nav-link i:hover{
      background-color: #5B3256;
      padding: 7px;
